<template>
  <b-list-group>
    <b-list-group-item  v-for="(conversation, i) in conversations" 
                        :key="i" 
                        :to="'chat/' + conversation.id" 
                        @click="setMessages(conversation.id)">
      {{conversation.name}}
    </b-list-group-item>
  </b-list-group>
</template>

<script>
export default {
  data () {
    return {}
  },
  computed: {
    conversations () {
      return this.$store.getters.conversations
    }
  },
  methods: {
    setMessages (id) {
      this.$store.dispatch('setMessages', id)
    }
  }
}
</script>

