import firebase from 'firebase/app'

class Conversation {
  constructor(name, type, imgSrc = '', password) {
    this.name = name
    this.type = type
    this.imgSrc = imgSrc
    this.password = password
  }
}

export default {
  state: {
    conversations: [],
    messages: [],
    users: [],
    curConv: null
  },
  mutations: {
    SET_CONVERSATIONS (state, payload) {
      state.conversations.push(payload)
    },
    SET_MESSAGES (state, payload) {
      state.messages.push(payload)
    },
    SET_USERS (state, payload) {
      state.users.push(payload)
    },
    SET_CUR_CONV (state, payload) {
      state.curConv = payload
    },
    CLEAR_MESSAGES (state) {
      state.messages = []
    }
  },
  actions: {
    async addMessage({getters}, {text}) {
      await firebase.database().ref('messages/' + getters.curConv).push({
        text,
        user: getters.user
      })
    },
    async setMessages( {commit}, convId) {
      await firebase.database().ref('messages/' + convId)
        .on('child_added', snapshot => { 
          let childData = snapshot.val()
          commit('SET_MESSAGES', childData)
        })
      commit('SET_CUR_CONV', convId)
      commit('CLEAR_MESSAGES')
    },
    async setConversations( {commit} ) {
      await firebase.database().ref('conversations').once('value', function(snapshot) {
        snapshot.forEach(function(childSnapshot) {
          var childData = childSnapshot.val()
          childData.id = childSnapshot.key

          commit('SET_CONVERSATIONS', childData)
        })
      })
    },
    async createChat ( undefind, {convName, convType = 'private', convImgSrc}) {
      await firebase.database().ref('conversations')
        .push( new Conversation(convName, convType, convImgSrc) )
    }
  },
  getters: {
    conversations: state => state.conversations,
    messages: state => state.messages,
    users: state => state.users,
    curConv: state => state.curConv
  }
}