<template>
  <b-row>
    <b-form ref="form" 
            @submit="onSubmit"
            class="form md-12 mx-auto">
      <b-form-group id="exampleInputGroup1"
                    label="Login:"
                    label-for="exampleInput1">
        <b-form-input id="exampleInput0"
                      type="text"
                      class="mb-1"
                      v-model="name"
                      required
                      placeholder="Enter name">
        </b-form-input>
        <b-form-input id="exampleInput1"
                      type="email"
                      class="mb-1"
                      v-model="email"
                      required
                      placeholder="Enter email">
        </b-form-input>
        <b-form-input id="exampleInput2"
                      type="password"
                      class="mb-1"
                      v-model="password"
                      required
                      placeholder="Enter password">
        </b-form-input>
        <b-form-input id="exampleInput3"
                      type="password"
                      v-model="confirmPassword"
                      required
                      placeholder="Enter password">
        </b-form-input>
      </b-form-group>
      
      <b-button type="submit" 
                variant="primary"
                @click="onSubmit"
                class="submit">
            Submit
        </b-button>
    </b-form>
  </b-row>
</template>

<script>
export default {
  data () {
    return {
      name: '',
      email: '',
      password: '',
      confirmPassword: ''
    }
  },
  computed: {
    loading () {
      return this.$store.getters.loading
    }
  },
  methods: {
    onSubmit (e) {
      e.preventDefault()

      const user = {
        email: this.email,
        password: this.password,
        name: this.name
      }

      this.$store.dispatch('registerUser', user)
        .then(() => {
          this.$router.push('/')
        })
        .catch(() => {})
    }
  }
}
</script>

<style scoped>
.form {
  width: 300px;
}

.submit {
  width: 100%;
}
</style>
