<template>
  <b-row>
    <b-col cols="12">
      <b-input-group>
        <b-form-input type="text" 
                      min="0.00"
                      v-model="textMessage"></b-form-input>

        <b-input-group-append @click="addMessage">
          <b-btn variant="secondary">Button</b-btn>
        </b-input-group-append>
      </b-input-group>
    </b-col>

    <b-col cols="12">
      <ul class="list-unstyled">
        <b-media  tag="li" 
                  v-for="(message, i) in messages" 
                  :key="i">
          <b-img slot="aside" blank blank-color="#abc" width="64" alt="placeholder" />
          <h5 class="mt-0 mb-1">{{ message.user.name || 'Error' }}</h5>
          {{message.text}}
        </b-media>
      </ul>
    </b-col>
  </b-row>
</template>

<script>
export default {
  props: ['id'],
  data () {
    return {
      textMessage: ''
    }
  },
  computed: {
    messages () {
      return this.$store.getters.messages || []
    },
    user () {
      return this.$store.getters.user
    }
  },
  methods: {
    addMessage () {
      this.$store.dispatch('addMessage', {text: this.textMessage} )
    }
  }
}
</script>

