<template>
  <div>
    <b-navbar toggleable="md" 
              type="dark" 
              variant="info">
      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
      <b-navbar-brand to='/rooms'>Rooms</b-navbar-brand>

      <b-collapse is-nav id="nav_collapse" class="justify-content-end">
        <b-navbar-nav>
          <b-nav-item
            v-for="(link, i) in links"
            :key="i"
            :to="link.url"
          >{{ link.title }}</b-nav-item>
          <b-nav-item
            @click="onLogout"
            v-if="isUserLoggedIn"
          >Logout</b-nav-item>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
    <b-container>
      <router-view></router-view>
    </b-container>
  </div>
</template>

<script>
export default {
  data () {
    return {
      sideNav: false
    }
  },
  methods: {
    onLogout () {
      this.$store.dispatch('logoutUser')
      this.$router.push('/')
    }
  },
  computed: {
    isUserLoggedIn () {
      return this.$store.getters.isUserLoggedIn
    },
    links () {
      if (this.isUserLoggedIn) {
        return [
          { title: 'Create Chat', icon: '', url: '/create-chat' }
        ]
      }
      else {
        return [
          { title: 'Login', icon: 'account_box', url: '/login' },
          { title: 'Register', icon: 'face', url: '/register' }
        ]
      }
    }
  }
}
</script>

